import './Form.css'
import {Formik} from 'formik';
import * as Yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import {sendingData} from './../../../redux/form-reducer'
import {displaysDateTime,displayMonth} from './../../../commom/createsDate.js'


const Form = () => {
const disp = useDispatch();

  let clientsArchive = useSelector((state) => state.tableReducer.customersArhive);
  let customers = useSelector((state) => state.tableReducer.customers);

  let id;
   if(clientsArchive.length != 0){
   let allClients = [...clientsArchive, ...customers];
   id = allClients.reduce((accum, item) => {
     if (item.id > accum) {
       return item.id;
     }
     return accum;
   }, 0);
    id = id + 1
   }
   
  
  const sentData = (dataUsers)=>{
    const date = displayMonth();
    const dateTime = displaysDateTime();
    const data = {...dataUsers, id, date, dateTime, status: 'new'} 
  
    disp(sendingData(data));
  }

  const SignupSchema = Yup.object().shape({
    name: Yup.string().typeError("  !").required(" ,  !"),
    phone: Yup.number().typeError("  !").required(" !"),
    email: Yup.string().email("  !").required(" !"),
  });

  function noDigits(event) {
    if ("1234567890".indexOf(event.key) != -1) event.preventDefault();
  }

  return (
    <Formik
      initialValues={{
        name: "",
        phone: "",
        email: "",
        product: "course-html",
      }}
      validateOnBlur
      onSubmit={(data) => {
        sentData(data);
      }}
      validationSchema={SignupSchema}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        setFieldValue,
      }) => (
        <form onSubmit={handleSubmit}>
          <label> :</label>
          <div className="form-group">
            <input
              type="text"
              name="name"
              onChange={handleChange}
              onBlur={handleBlur}
              className="form-control"
              placeholder="  "
              value={values.name}
              onKeyPress={(e) => noDigits(e)}
            />

            {errors.name && touched.name && (
              <p className="error">{errors.name}</p>
            )}
          </div>
          <div className="form-group">
            <input
              type="text"
              name="phone"
              onChange={handleChange}
              onBlur={handleBlur}
              className="form-control"
              placeholder=""
            />
            {errors.phone && touched.phone && (
              <p className="error">{errors.phone}</p>
            )}
          </div>
          <div className="form-group">
            <input
              type="email"
              name="email"
              onChange={handleChange}
              onBlur={handleBlur}
              className="form-control"
              placeholder="Email"
            />
            {errors.email && touched.email && (
              <p className="error">{errors.email}</p>
            )}
          </div>
          <div className="form-group">
            <label htmlFor="exampleFormControlSelect1">:</label>
            <select
              name="product"
              onChange={handleChange}
              className="form-control"
            >
              <option data-opt value="course-html">
                  
              </option>
              <option data-opt value="course-js">
                  JavaScript
              </option>
              <option data-opt value="course-vue">
                  VUE JS
              </option>
              <option data-opt value="course-php">
                  PHP
              </option>
              <option data-opt value="course-wordpress">
                  WordPress
              </option>
            </select>
          </div>
          <div className="form-group">
            <button
              disabled={isSubmitting}
              type="submit"
              className="btn btn-lg btn-primary btn-block"
            >
               
            </button>
          </div>
        </form>
      )}
    </Formik>
  );
}
 
export default Form;