import PanelNavigation from "./Panel/PanelNavigation";
import Customers from "./ustomers/ustomers";
import { useState, useEffect} from 'react';
import { useDispatch, useSelector } from "react-redux";
import { getData} from "./../../redux/table-reducer";
import Navigate from "../Navigate/Navigate";
 import { setInitialZed } from "./../../redux/edit-reducer";
 import './TablePage.css'


const TablePage = () => {

  const disp = useDispatch();
  const statusArr = useSelector((state) => state.tableReducer.statusArr);
  const clientele = useSelector((state) => state.tableReducer.customers);
  const error = useSelector((state) => state.tableReducer.error);
  const clienteleArhive = useSelector((state) => state.tableReducer.customersArhive);
  const loading = useSelector((state) => state.tableReducer.load);

  const [active, setActive] = useState('');
  const [product, setProduct] = useState('');
  const [status, setStatus] = useState('');
  
  useEffect(() => {
    disp(getData());
    disp(setInitialZed(false));
  }, []);

 
  useEffect(()=>{
    if (typeof localStorage.getItem("filter") !== "object") {
      let storage = localStorage.getItem("filter");
      storage = JSON.parse(storage);
      const [oldProduct, oldStatus, oldId] = storage;
      if (oldProduct === "" || oldStatus === "" || oldId === "") {
        setProduct("all-products");
        setStatus("all-together");
        setActive(1);
      } else {
        setProduct(oldProduct);
        setStatus(oldStatus);
        setActive(oldId);
      }
    } else {
      setProduct("all-products");
      setStatus("all-together");
      setActive(1);
    }
  },[])


   useEffect(() => {
     let filter = [product, status, active];
     localStorage.setItem("filter", JSON.stringify(filter));
   }, [product, status, active]);


  const filteringStatus = ()=>{
    if (status === "all-together" && product === "all-products") {
      return clientele;
    }else if(status === 'arhive'){
      return product === "all-products" ? clienteleArhive : clienteleArhive.filter(item => item.product === product)
    }
   return status ===  "all-together" ? clientele.filter((item) => item.product === product) : (product === "all-products" ? clientele.filter((item) => item.status === status) : clientele.filter((item) => item.status === status).filter((item) => item.product === product))
  }

  const changesStatus = (e,it, id)=>{
   e.preventDefault()
   setStatus(it);
   setActive(id)
  }

   const changesProduct = (e) => {
    setProduct(e.target.value)
   };

  const filterData = filteringStatus();

  if (loading) {
    return <div className="load">
        <div className="load_text"> </div>
     </div>
  }

  return (
    <div className="with-nav body--dashboard">
      <Navigate />
      {error ? (
        <div style = {{fontSize:"25px", textAlign: "center"}}> : {error}</div>
      ) : (
        <div>
          <PanelNavigation active={active} changesStatus={changesStatus} />
          <div className="main-wrapper">
            <div className="container-fluid">
              <div className="admin-heading-1"> </div>
              <form>
                <div className="row mb-3 justify-content-start">
                  <div className="col">
                    <div
                      className="btn-group"
                      id="allBtn"
                      role="group"
                      aria-label="...">
                      {statusArr.map((item) => {
                        return (
                          <a
                            key={item.id}
                            onClick={(e) => changesStatus(e, item.status, item.id)}
                            href="#"
                            className={
                              active === item.id
                                ? "active " + "btn btn-light"
                                : "btn btn-light"}>
                            {item.text}
                          </a>
                        );
                      })}
                    </div>
                  </div>
                  <div className="col">
                    <select
                      value={product}
                      onChange={changesProduct}
                      className="custom-select">
                      <option value="all-products"> </option>
                      <option value="course-html">  </option>
                      <option value="course-js">  JavaScript</option>
                      <option value="course-vue">  VUE JS</option>
                      <option value="course-php">  PHP</option>
                      <option value="course-wordpress">
                          WordPress
                      </option>
                    </select>
                  </div>
                </div>
              </form>
              <Customers filterData={filterData} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
 
export default TablePage;