import { useSelector } from "react-redux";
// import ava from "./../../../../public/images/avatar-128.jpg";

const PanelNavigation = ({active, changesStatus}) => {
  const clientele = useSelector((state) => state.tableReducer.customers);
  const nav = useSelector((state) => state.tableReducer.nav);
  const data = useSelector((state) => state.tableReducer.customers);
  const clienteleArhive = useSelector((state) => state.tableReducer.customersArhive);
  

  const calculatesNumberCourses = (status)=>{
    if (status === "all-together") return clientele.length
    if (status === "arhive") return clienteleArhive.length
      return clientele.filter((item) => item.status === status).length;
  }

  return (
    <div className="left-panel blue-skin">
      <div className="left-panel__logo">
        <div className="left-panel__logo-title">CRM </div>
        <div className="left-panel__logo-subtitle">
            webcademy
        </div>
      </div>

      <div className="left-panel__user clearfix">
        <div className="left-panel__user-photo">
          <img src="./images/avatar-128.jpg" alt="Avatar" />
        </div>
        <div className="left-panel__user-name">
           <br />
          
        </div>
      </div>

      <div className="left-panel__navigation">
        <div className="left-panel__navigation-title"></div>
        <ul id="one">
          {nav.map((item) => {
            return (
              <li
                onClick={(e) => changesStatus(e,item.status, item.id)}
                key={item.id}>
                <a href="#" className={active === item.id ? "active" : null}>
                  {item.text}
                  <div className="badge">
                    {calculatesNumberCourses(item.status)}
                  </div>
                </a>
              </li>
            );
          })}
        </ul>
      </div>

      <div className="left-panel__navigation">
        <div className="left-panel__navigation-title"></div>
        <ul>
          <li>
            <a href="#"></a>
          </li>
          <li>
            <a href="#"></a>
          </li>
        </ul>
      </div>

      <div className="left-panel__navigation left-panel__navigation--no-line">
        <ul>
          <li>
            <a href="#"></a>
          </li>
        </ul>
      </div>
    </div>
  );
}
 
export default PanelNavigation;