  
  
  export function displaysDateTime() {
    let now = new Date();
    let v = now.toLocaleTimeString();
    let c = now.getDate();
    let n = now.getMonth() + 1;
    let z = now.getFullYear();
    let r = `${z}-${n}-${c} ${v}`
    return r;
  }


  export function displayMonth() {
    let now = new Date().toLocaleDateString();
    return now;
  }