import * as axios from "axios";


const instance = axios.create({
   baseURL: 'https://experienced-incongruous-dinner.glitch.me/'
})


export const userData = {
posData(data){
   return instance.post(`applications`, data);
  }

}


export const customersAPI = {
  getCustomers(){
    return instance.get(`applications`);
  },
  getClientArchive(){
    return instance.get(`archive`);
  }
}


export const editAPI = {
  updateClient(data, id) {   
    return instance.put(`applications/${id}`, data);
  },
  updateClientArhive(data, id){
     return instance.put(`archive/${id}`, data);
  },
  deleteClient(id){
    return instance.delete(`applications/${id}`);
  },
  deleteClientArhive(id){
    return instance.delete(`archive/${id}`);
  },
  addClientArchive(client) {
    return instance.post(`archive`, client);
  }
}
