import "./../../libs/bootstrap/css/bootstrap.min.css";
import "./../../css/main.css";
import Title from "./Title/Title";
import Form from "./Form/Form";
import { useSelector } from "react-redux";
import SentPage from "../SentPage/SentPage";
import Navigate from "../Navigate/Navigate";


const FormPage = () => {
  const val = useSelector((state) => state.formReducer.sent);
  const error = useSelector((state) => state.formReducer.error);


  
  return ( 
<div className="with-nav radial-bg flex-center mix">
  <Navigate/>
  <div className="white-plate white-plate--payment">
    <div className="container-fluid">
        <Title/>
        <div className="white-plate__line-between white-plate__line-between--main"></div>
        {
         !error ? (val ?  <SentPage/> : <Form/>) : <div style={{color: 'red', fontSize: '20px', textAlign:'center'}}>{error} </div>
        }
        
    </div>
  </div>
</div>
   );
}
 
export default FormPage;