import { Link } from "react-router-dom";


export const Customers = ({ filterData }) => {


  function findsProduct(item) {
    const data = new Map([
      ["course-html", "  "],
      ["course-js", "  JavaScript"],
      ["course-vue", "  VUE JS"],
      ["course-php", "  PHP"],
      ["course-wordpress", "  WordPress"],
      ["all-products", " "],
    ]);

    for (const [key, meaning] of data) {
      if (key === item) {
        return meaning;
      }
    }
  }

  function findsStatus(item) {
    const data = new Map([
      ["new", ""],
      ["work", " "],
      ["payment-expected", " "],
      ["completed", ""],
      ["rejection", ""],
    ]);

    for (const [key, meaning] of data) {
      if (key === item) {
        return meaning;
      }
    }
  }

  function getBageColor(status) {
    const data = new Map([
      ["work", "badge-warning"],
      ["completed", "badge-success"],
      ["rejection", "badge-cyan"],
      ["payment-expected", "badge-darkviolet"],
      ["new", "badge-danger"],
    ]);

    for (const [key, value] of data) {
      if (key === status) {
        return value;
      }
    }
  }

  return (
    <table className="table fs-14">
      <thead>
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th>email</th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      {
        <tbody id="container">
          {filterData.map((item) => {
            let product = findsProduct(item.product);
            let status = findsStatus(item.status);
            let color = getBageColor(item.status);
            return (
              <tr key={item.id}>
                <th scope="row">{item.id}</th>
                <td>{item.date}</td>
                <td>{product}</td>
                <td>{item.name}</td>
                <td>{item.email}</td>
                <td>{item.phone}</td>

                <td>
                  <div className={`badge badge-pill ${color}`}>{status}</div>
                </td>
                <td>
                  <Link to={`/edit/${item.id}`} href="#">
                    
                  </Link>
                </td>
              </tr>
            );
          })}
        </tbody>
      }
    </table>
  );
};
 
export default Customers;