import {editAPI} from './../api/api'


const initialState = {
  initialized: null,
  errors: null
}

const SET_INITIALIZED = 'SET_INITIALIZED';
const SET_ERRORS = 'SET_ERRORS';

export const editReducer = (state=initialState, action)=>{
   switch(action.type){
     case SET_INITIALIZED: 
     return {...state, initialized: action.val}
     case SET_ERRORS:
      return {...state, errors: action.err}
   }
    return state
}


export const setInitialZed = (val) => ({
  type: SET_INITIALIZED,
  val
})

export const setErrors = (err) => ({
  type: SET_ERRORS,
  err
})

export const updatesData = (data, id)=>{
  return(dispatch)=>{
   editAPI.updateClient(data, id)
   .then(()=>{
     dispatch(setInitialZed(true))
   })
   .catch(()=>{
     dispatch(setInitialZed(false))
   })
  }
}


export const updatesDataArhive = (data, id)=>{
  return(dispatch)=>{
   editAPI.updateClientArhive(data, id)
   .then(()=>{
     dispatch(setInitialZed(true))
   })
   .catch(()=>{
     dispatch(setInitialZed(false))
   })
  }
}


export const del = (id) => {
  return (dispatch) => {
    return editAPI.deleteClient(id)
     .then(() => {
         dispatch(setInitialZed(true))
       })
       .catch((er) => {
         dispatch(setInitialZed(false))
         dispatch(setErrors(er.message))
       })
  }
}


export const delArhive = (id) => {
  return (dispatch) => {
    return editAPI.deleteClientArhive(id)
     .then(() => {
         dispatch(setInitialZed(true))
       })
       .catch((er) => {
         dispatch(setInitialZed(false))
         dispatch(setErrors(er.message))
       })
  }
}


export const add = (client) => {
  return (dispatch) => {
  return editAPI.addClientArchive(client)
  }
}







