import {customersAPI} from './../api/api'


const GET_CUSTOMERS = 'GET_CUSTOMERS';
const LOADING = 'LOADING';
const GET_CUSTOMERS_ARHIVE = 'GET_CUSTOMERS_ARHIVE'
const ADD_ERROR = 'ADD_ERROR'

const initialState = {
 customers: [],
 customersArhive: [],
 load: true,
 statusArr: [
    { id: 1, text: "", status: "all-together" },
    { id: 2, text: "", status: "new" },
    { id: 3, text: " ", status: "work" },
    { id: 4, text: "", status: "completed" },
    { id: 5, text: "", status: "arhive" },
  ],
  nav: [
    { id: 1, text: " ", status: "all-together" },
    { id: 2, text: "", status: "new" },
    { id: 3, text: " ", status: "work" },
    { id: 4, text: "", status: "completed"  },
    { id: 5, text: "", status: "arhive" },
  ],
  error: null
}


export const tableReducer = (state=initialState, action)=>{
  switch(action.type){
    case GET_CUSTOMERS:
      return {...state, customers: action.data}
    case GET_CUSTOMERS_ARHIVE:
      return {...state, customersArhive: action.data}
    case LOADING:
      return {...state, load: action.bool}
    case ADD_ERROR:
      return {...state, error: action.er}
    default:
      return state
  }
}


export const addCustomers = (data)=>({type: GET_CUSTOMERS, data})
export const setLoading = (bool) => ({type: LOADING, bool})
export const addCustomersArhive = (data) => ({type: GET_CUSTOMERS_ARHIVE, data})
       const setError = (er) => ({type: ADD_ERROR, er})

export const getData = ()=>{
  
  return(dispatch)=>{
    let c = customersAPI.getCustomers();
    let b = customersAPI.getClientArchive();
    Promise.all([c, b]).then(data => {
     const [customers, clientsArchive] = data
        dispatch(setLoading(false));
        dispatch(addCustomers(customers.data));
        dispatch(addCustomersArhive(clientsArchive.data));
      })
      .catch(er=>{
       dispatch(setError(er.message))
      })

  }
}


export const setCustomersArhive = () => {

  return (dispatch) => {
    customersAPI.addClientArchive()
      .then(data => {
        dispatch(addCustomersArhive(data.data))
      })

  }
}