export const requiredField = value => {

   
  if (value){
     return undefined
  }
  return ' !';

}


export const numberField = value => {
    
  if (value && isNaN(Number(value))) {
     return ' !';
  }
  return undefined;
}


export const mailField = value => {
    
  if (value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
     return '  !';
  }
  return undefined;
}



