import Form from "./Form/Form";
import {useParams,Link, Navigate} from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import ErrorPage from "./ErrorPage/ErrorPage";
import { displaysDateTime, displayMonth } from "./../../commom/createsDate";
import { updatesData, del, add, delArhive, updatesDataArhive } from "./../../redux/edit-reducer";
import { useState} from "react";


const EditPage = () => {
  const { id } = useParams();
  const disp = useDispatch();
  let customers = useSelector((state) => state.tableReducer.customers);
  const clienteleArhive = useSelector((state) => state.tableReducer.customersArhive);
  let initialized = useSelector((state) => state.editReducer.initialized);
  let errors = useSelector((state) => state.editReducer.errors);

  const [arhive, setArhive] = useState(false)
 

  if (customers.length === 0 && clienteleArhive.length === 0) {
    return <ErrorPage />;
  }
 
   if (initialized) {
     return <Navigate to="/table" />;
   }

   const getClients = ()=>{
    let customersData = customers.find(item =>{
      return item.id == id
    })

    let customersArhive = clienteleArhive.find((item) => {
      return item.id == id;
    });
    if (typeof customersData == "undefined" && !arhive) {
      setArhive(true);
    }
    return typeof customersData == "undefined" ? customersArhive : customersData; 
  }

   let client = getClients();


  const onSubmit = (formData) => {
      let dateTime = displaysDateTime();
      let date = displayMonth();
      let data = { ...formData, dateTime, date };
        if(arhive){
         disp(updatesDataArhive(data, id));
        }else{
         disp(updatesData(data, id));
        }
       
  };

 

  const addArhive =  (client)=>{
       disp(add(client))
       .then(()=>{
        disp(del(id)); 
       })
       
       
        
  }

  const deleteClient = ()=>{
     if (arhive) {
       disp(delArhive(id));
     } else {
       disp(del(id));
     }
      
  }

  
  const { product, name, email, phone, status, dateTime } = client;

  const initialValues = {
    product: `${product}`,
    name: `${name}`,
    email: `${email}`,
    phone: `${phone}`,
    status: `${status}`,
  };

 
  return (
    <div id="two" className="with-nav body--dashboard">
      <div className="main-wrapper">
        <div className="container-fluid">
          <div className="row justify-content-between align-items-center">
            <div className="col">
              <div className="admin-heading-1">  </div>
              {errors ? (
                <div>   ! {errors}</div>
              ) : (
                ""
              )}
            </div>
            <div className="col text-right">
              <Link to="/table" className="btn btn-link">
                 
              </Link>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <Form
                id={id}
                initialValues={initialValues}
                onSubmit={onSubmit}
                client={client}
                dateTime={dateTime}
                addArhive={addArhive}
                deleteClient={deleteClient}
                arhive={arhive}/>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
 
export default EditPage;