import {userData} from './../api/api'

const SET_VALUE = 'SET_VALUE';
const SET_ERROR = 'SET_ERROR';

const initialState = {
  sent: false,
  error: null
}


export const formReducer = (state = initialState, action)=>{
  switch(action.type){
    case SET_VALUE:
     return {...state, sent: action.val}

    case SET_ERROR:
    return  {...state, error: action.er}
    
    default:
      return state;
  }
}


export const setSent =(val)=> ({type: SET_VALUE, val})

export const setError =(er)=> ({type: SET_ERROR, er})

export const sendingData = (data) =>{
   
   return (dispatch)=>{
   userData.posData(data)
   .then(res => {
      dispatch(setSent(true))
   })
   .catch(er=>{
    dispatch(setError(er.message))
    
   })
   }
}