
import { useDispatch } from 'react-redux';
import {setSent} from './../../redux/form-reducer'
const SentPage = () => {
  
  const disp = useDispatch();

  setTimeout(()=>{
   disp(setSent(false));
  },3000)

  return ( 
      <div style = {{fontSize: '35px', textAlign: 'center', color: 'green'}}>
           !
      </div>
   );
}
 
export default SentPage;