import { reduxForm, Field } from "redux-form";
import { Input } from "../../../commom/FormControls/formControls";
import { requiredField, numberField,mailField } from "../../../utils/validarors";

const Form = (props) => {
  
  return (
    <form>
      <div className="card mb-4">
        <div className="card-header">  </div>
        <div className="card-body">
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>ID:</strong>
            </div>
            <div id="number" className="col">
               {props.id}
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong> :</strong>
            </div>
            <div id="date" className="col">
              {props.dateTime}
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>:</strong>
            </div>
            <div className="col">
              <Field
                validate={[]}
                name="product"
                component="select"
                className="custom-select"
              >
                <option value="course-html">  </option>
                <option value="course-js">  JavaScript</option>
                <option value="course-vue">  VUE JS</option>
                <option value="course-php">  PHP</option>
                <option value="course-wordpress">  WordPress</option>
              </Field>
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>:</strong>
            </div>
            <div className="col">
              <Field
                validate={[requiredField]}
                name="name"
                className="form-control"
                placeholder=" "
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Email:</strong>
            </div>
            <div className="col">
              <Field
                name="email"
                validate={[requiredField, mailField]}
                className="form-control"
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>:</strong>
            </div>
            <div className="col">
              <Field
                name="phone"
                validate={[requiredField, numberField]}
                className="form-control"
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong> :</strong>
            </div>
            <div className="col">
              <Field
                validate={[requiredField]}
                name="status"
                component="select"
                className="custom-select"
              >
                <option value="">...</option>
                <option value="new"></option>
                <option value="work"> </option>
                <option value="payment-expected"> </option>
                <option value="completed"></option>
                <option value="rejection"></option>
              </Field>
            </div>
          </div>
        </div>
      </div>

      <div className="row justify-content-between">
        <div className="col">
          <button onClick={props.handleSubmit} className="btn btn-primary">
             
          </button>
        </div>
        <div className="col text-right">
          <button disabled = {props.arhive} type="button" onClick={()=>props.addArhive(props.client)}
            className="btn btn-dark">
              
          </button>
            <button onClick={props.deleteClient} style = {{margin: "5px"}}
             type="button"
             className="btn btn-dark">
            
          </button>
        </div>
      </div>
    </form>
  );
}


 const ReduxForm = reduxForm({
  form: 'application'
 })(Form)




export default ReduxForm;